import React from 'react';
import Checkbox from '../checkbox';
import Button from '../button';

export default class Dropdown extends React.Component {
    displayName = 'Dropdown';
    static propTypes = {
      className: React.PropTypes.string,
      options: React.PropTypes.array,
      disabled: React.PropTypes.bool,
      label: React.PropTypes.string,
      onChange: React.PropTypes.func,
      template: React.PropTypes.func,
      value: React.PropTypes.any,
      name: React.PropTypes.string.isRequired,
      multi: React.PropTypes.bool
    };
    static defaultProps = {
      className: '',
      options: [],
    };
    constructor(props) {
      super(props);
      this.state = {active: false};
    }
    handleOpenPosition = (event) => {
      const client = event.target.getBoundingClientRect();
      const screenHeight = window.innerHeight || document.documentElement.offsetHeight;
      this.setState({
        active: true,
        up: client.top > ((screenHeight / 2) + client.height)
      });
    };
    handleSelection = ({key, value, index}) => {
      if (!this.props.multi) {
        // this.handleApply({key, value, index});
        this.handleClose();
      }
    };
    handleApply = ({key, value, index}) => {
      const name = key;
      this.props.onChange({name, value, index});
    };
    handleClose = () => {
      this.setState({active: false});
    };
    renderValues = () => {
      let items = null;
      if (this.props.template) { items = this.props.template(this.props.options); }
      if (!this.props.template && !this.props.multi) {
        items = this.props.options.map( (item, index) => {
          let className;
          if (item.value === this.props.value) { className = `select-item is-selected`; }
          return (
            <li key={index} className={className} onClick={this.handleSelection.bind(this, {key: this.props.name, value: item.value})} >
              { item.label }
            </li>
          );
        });
      } else {
        const options = this.props.options.map( (item, index) => {
          let className;
          if (item.value === this.props.value) className = `select-item is-selected`;
          return (
            <li key={index} className={className} onClick={this.handleSelection.bind(this, {key: this.props.name, value: item.value})} >
              <Checkbox onChange={this.handleApply.bind(this, {key: this.props.name, value: item.value, index: index})} name={item.label} label={item.label} value={item.value} />
            </li>
          );
        });
        items = (
          <div className="o-multi-options">
            {options}
            <Button format="raised" label="Done" onClick={this.handleClose} />
          </div>
        );
      }

      let className = 'dropdown__values';
      if (this.state.up) className += ' is-up';
      return <ul ref="values" className={className} >{ items }</ul>;
    };
    render() {
      let className = 'dropdown';
      let labelIndex = 0;
      this.props.options.forEach((item, index) => {
        if (item.value === this.props.value) { labelIndex = index; }
      });
      if (this.props.className) className += `${this.props.className}`;
      if (this.props.disabled) className += ' is-disabled';
      if (this.state.active) className += ' is-active';

      return (
        <div className={className}>
            {this.props.label ? <label className="dropdown__label">{this.props.label}</label> : ''}
            { this.renderValues() }
            <div ref="value" className="dropdown__value" onClick={this.handleOpenPosition}>
             <span>{this.props.options[labelIndex].label}</span>
            </div>
        </div>
      );
    }
}



