import React, { PropTypes, Component } from 'react';
import ItemTypes from './ItemTypes';
import { DropTarget } from 'react-dnd';
import CloseIcon from 'react-material-icons/icons/navigation/close';

const boxTarget = {
  drop() {
    return { name: 'table group' };
  }
};

@DropTarget(ItemTypes.BOX, boxTarget, (connect, monitor) => ({
  connectDropTarget: connect.dropTarget(),
  isOver: monitor.isOver(),
  canDrop: monitor.canDrop()
}))
export default class DragTarget extends Component {
  static propTypes = {
    connectDropTarget: PropTypes.func.isRequired,
    isOver: PropTypes.bool.isRequired,
    canDrop: PropTypes.bool.isRequired
  };
  handleClick =(col) => {
    this.props.removeItem(col);
  };
  renderItems = () => {
    if (this.props.columns && this.props.columns.size > 0) {
      return Array.from(this.props.columns).map((col, index) => {
        return (
          <div onClick={this.handleClick.bind(this, col)} key={index}>{col}
          <CloseIcon/>
          </div>
        );
      }); 
    } else {
      return (<span>Drag Column Headers Here</span>);
    }
  };
  render() {
    const { canDrop, isOver, connectDropTarget } = this.props;
    const isActive = canDrop && isOver;
    const className= isActive ? 'o-drag-drop isActive' : 'o-drag-drop';
    return connectDropTarget(
      <div className={className}>
        {this.renderItems()}
       <button onClick={this.props.handleReset}>RESET</button>

      </div>
    );
  }
}


