import React from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin';
import Ripple from '../ripple';
import events from '../utils/events';
import mixin from 'mixin-decorator';

@mixin(PureRenderMixin)
export default class Switch extends React.Component {
  static displayName = 'Switch';
  static propTypes = {
    value: React.PropTypes.bool,
    className: React.PropTypes.string,
    disabled: React.PropTypes.bool,
    label: React.PropTypes.string,
    name: React.PropTypes.string,
    onBlur: React.PropTypes.func,
    onFocus: React.PropTypes.func,
    onChange: React.PropTypes.func,
  };
  static defaultProps = {
    value: false,
    className: '',
    disabled: false
  };
  handleInputClick = (event) => {
    event.preventDefault();
    const name = this.props.name;
    const value = !this.props.value;
    if (this.props.onChange) {
      this.props.onChange({name, value});
    }
  };
  handleMouseDown = (event) => {
    if (!this.props.disabled) this.refs.ripple.start(event);
  };
  render() {
    let labelClassName = this.props.disabled ? 'o-switch isDisabled' : 'o-switch';
    let switchClassName = this.props.value ? 'o-switch__box isChecked' : 'o-switch__box';
    if (this.props.className) labelClassName += ` ${this.props.className}`;

    return (
      <label
        className={labelClassName}
        onClick={this.handleInputClick}
      >
        <input
          {...this.props}
          type='checkbox'
          checked={this.props.value}
          className='o-switch__input'
        />
        <span role='switch' className={switchClassName}>
          <span role='thumb' className='o-switch-box__thumb' onMouseDown={this.handleMouseDown}>
            <Ripple ref='ripple' role='ripple' className='o-switch-ripple' spread={2.4} centered />
          </span>
        </span>
        { this.props.label ? <span className='o-switch__label'>{this.props.label}</span> : null }
      </label>
    );
  }
};



