import React from 'react';

export default class Image extends React.Component {
  static propTypes = {
    imageUrl: React.PropTypes.string,
    alt: React.PropTypes.string
  };
  render() {
    return (
      <img src={this.props.imageUrl} alt={this.props.alt} />
    );
  }
}



