import { registerComponents, registerComponent } from 'core-ui';
import ShowLifeCycle from '../../containers/devtools/lifecycle';
import UI from '../../components';

if (process.env.NODE_ENV !== 'production') {
  registerComponent(ShowLifeCycle);
}

registerComponents(UI);



