import React from 'react';
import Input from '../input';
import View from '../view';

export default class TableCell extends React.Component {
  static propTypes = {
    className: React.PropTypes.string,
    handleClick: React.PropTypes.func,
    index: React.PropTypes.number,
    editable: React.PropTypes.bool,
    handleChange: React.PropTypes.func,
    value: React.PropTypes.any,
    data: React.PropTypes.any,
  };
  constructor(...args) {
    super(...args);
    this.state = {
      isActive: false,
      value: ''
    };
  }
  handleClick = (props) => {
    if (this.props.handleClick) { this.props.handleClick(props); }
    console.log(props);
    this.setState({isActive: !this.state.isActive});
  };
  handleEditableBlur = () => {
    this.setState({isActive: false});
  };
  renderContent = () => {
    let className = 'o-table-cell';
    return (
      <View 
        className={className}
        data-value={this.props.value}
        onClick={this.handleClick.bind(this, this.props) }>
        {this.props.data}
      </View>
    );
  };
  handleChange = (name, event) => {
    this.setState({value: event.target.value})
  };
  renderEditableContent = () => {
    return (
      <Input
        name={this.props.value}
        onChange={this.handleChange }
        onBlur={this.handleEditableBlur}
        value={this.state.value}
        label={this.props.column} 
        />
    );
  };
  render() {
   let className = 'o-table-cell-wrapper';
     if (this.props.column) className += ` column-${this.props.column.replace(/_/g, '-').toLowerCase()}`;

    return (
      <div className={className} data-val={this.props.column}>
        { this.state.isActive ? this.renderEditableContent() : this.renderContent() }
      </div>
    );
  }
}



