import React from 'react';
import FontIcon from '../font_icon';
import Menu from './menu';
import Ripple from '../ripple';
import mixin from 'mixin-decorator';
import PureRenderMixin from 'react-addons-pure-render-mixin';

@mixin(PureRenderMixin)
export default class IconMenu extends React.Component {
  static displayName = 'IconMenu';
  static propTypes = {
    className: React.PropTypes.string,
    icon: React.PropTypes.string,
    iconRipple: React.PropTypes.bool,
    menuRipple: React.PropTypes.bool,
    onClick: React.PropTypes.func,
    onSelect: React.PropTypes.func,
    position: React.PropTypes.string,
    selectable: React.PropTypes.bool,
    onHide: React.PropTypes.func,
    onShow: React.PropTypes.func,
    children: React.PropTypes.any
  };
  static defaultProps = {
    className: '',
    icon: 'more-vert',
    iconRipple: true,
    menuRipple: true,
    position: 'auto',
    selectable: false
  };
  handleButtonClick = () => {
    this.refs.menu.show();
    if (this.props.onClick) this.props.onClick();
  };
  handleMouseDown = (event) => {
    if (this.props.iconRipple) {
      this.refs.ripple.start(event);
    }
  };
  render() {
    let className = 'o-icon-menu';
    if (this.props.className) className += ` ${this.props.className}`;

    return (
      <div className={className}>
        <FontIcon
          className="o-icon-menu__icon"
          onClick={this.handleButtonClick}
          onMouseDown={this.handleMouseDown}
          value={this.props.icon}
        />
        <Menu
          ref="menu"
          onHide={this.props.onHide}
          onSelect={this.props.onSelect}
          onShow={this.props.onShow}
          position={this.props.position}
          ripple={this.props.menuRipple}
          selectable={this.props.selectable}
        >
          { this.props.children }
        </Menu>
        { this.props.iconRipple ? <Ripple ref="ripple" className="o-icon-menu-ripple" spread={2.4} centered /> : null }
      </div>
    );
  }
}



