import React from 'react';

export default class ListSubHeader extends React.Component {
  static displayName = 'ListSubHeader';
  static propTypes = {
    caption: React.PropTypes.string
  };
  render() {
    return (
      <h5 className="o-list__subheader">
        { this.props.caption }
      </h5>
    );
  }
}



