import React from 'react';
import OverflowIcon from 'react-material-icons/icons/navigation/more-vert';
import Menu from '../menu';
const {IconMenu, MenuItem} = Menu;
export default class OverflowButton extends React.Component {
  render() {
    return (
    <IconMenu icon='more_vert' position='top-left' menuRipple>
    <MenuItem value='download' icon='get_app' caption='Download' />
    <MenuItem value='help' icon='favorite' caption='Favorite' />
    <MenuItem value='settings' icon='open_in_browser' caption='Open in app' />
  </IconMenu>
    );
  }
}


