import React from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin';
import FontIcon from '../font_icon';
import Ripple from '../ripple';
import mixin from 'mixin-decorator';

@mixin(PureRenderMixin)
export default class MenuItem extends React.Component {
  static displayName = 'MenuItem';
  static propTypes = {
    caption: React.PropTypes.string,
    disabled: React.PropTypes.bool,
    icon: React.PropTypes.string,
    ripple: React.PropTypes.bool,
    shortcut: React.PropTypes.string,
    selected: React.PropTypes.bool,
    onClick: React.PropTypes.func,
    className: React.PropTypes.string
  };
  static defaultProps = {
    disabled: false,
    ripple: false,
    selected: false
  };
  handleClick = (event) => {
    if (this.props.onClick && !this.props.disabled) {
      this.props.onClick(event, this);
    }
  };
  handleMouseDown = (event) => {
    if (this.props.ripple && !this.props.disabled) {
      this.refs.ripple.start(event);
    }
  };
  render() {
    let className = 'o-menu-item';
    if (this.props.className) className = `${this.props.className}`;
    if (this.props.selected) className += ' isSelected';
    if (this.props.disabled) className += ' isDisabled';

    return (
      <li
        className={className}
        onClick={this.handleClick}
        onMouseDown={this.handleMouseDown}
      >
        { this.props.icon ? <FontIcon value={this.props.icon} className="o-font-icon"/> : null }
        <span className="o-menu-item__caption">{this.props.caption}</span>
        { this.props.shortcut ? <small className="o-menu-item__shortcut">{this.props.shortcut}</small> : null }
        { this.props.ripple ? <Ripple ref="ripple" className="o-menu-item__ripple" spread={2.5} /> : null }
      </li>
    );
  }
}



