import React from 'react';
import TableColumn from './TableColumn'; 
import View from '../view';
import { DragDropContext } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';
import ViewportMetrics from 'react/lib/ViewportMetrics';
import DragTarget from './DragTarget';

@DragDropContext(HTML5Backend)
export default class TableColumns extends React.Component {

  static propTypes = {
    handleColumnClick: React.PropTypes.func,
    columnComponent: React.PropTypes.any,
    columns: React.PropTypes.array,
    hiddenColumns: React.PropTypes.array,
    fixedHeader: React.PropTypes.bool,
    dataSource: React.PropTypes.array
  };
  constructor(...args) {
    super(...args);
    this.state = {
      sortedColumns: new Set()
    };
  }
  handleDrag = (name, table) => {
    const columns = this.state.sortedColumns;
    columns.add(name);
    this.setState({sortedColumns: columns});
    this.props.handleDrag(columns);
  };
  removeItem = (col) => {
    const columns = this.state.sortedColumns;
    columns.delete(col);
    this.setState({sortedColumns: columns});
    this.props.handleDrag(columns);
  };
  handleReset = () => {
    this.setState({sortedColumns: new Set()});
    this.props.handleReset();
  };
  renderColumns = () => {
    const Column = this.props.columnComponent;
    const data = this.props.columns ? this.props.columns: Object.keys(this.props.dataSource[0]);
    const columns = data.map((column, index) => {
      if (this.props.hiddenColumns.includes(index)) {
        return;
      }
      if (Column) {
        return (
          <Column
            {...this.props}
            data={column}
            index={index} />
        );
      }
      const display = typeof column === 'string' ? column : column.displayName;
      const value = typeof column === 'string' ? column : column.value;
      //this.props.visibleColumns && !this.props.visibleColumns.indexOf(value) > -1
      if (value) {
        return (
          <TableColumn key={index}
            name={column}
            displayName={display}
            handleDrag={this.handleDrag}
            handleColumnClick={this.props.handleColumnClick}
            data={column}
            index={index} />
        );
      }
    }).filter((item) => {return item});
    return columns;
  };
  render() {
    return (
        <div className="o-table-header">
            <div className="o-table-header__drag-area">
              <DragTarget handleReset={this.handleReset} removeItem={this.removeItem} columns={this.state.sortedColumns} />
            </div>
            <div className="o-table-header__columns" >
               {this.renderColumns()}
            </div>
        </div>
    );
  }
}



