import React from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin';
import FontIcon from '../font_icon';
import mixin from 'mixin-decorator';
import {Link as RouterLink} from 'react-router';

@mixin(PureRenderMixin)
export default class Link extends React.Component {
  static displayName = 'Link';
  static propTypes = {
    label: React.PropTypes.string,
    className: React.PropTypes.string,
    count: React.PropTypes.number,
    icon: React.PropTypes.string,
    onClick: React.PropTypes.func,
    route: React.PropTypes.string
  };
  static defaultProps = {
    attributes: '',
    className: ''
  };
  render() {
    let className = 'o-link';
    if (this.props.className) className = `${this.props.className}`;
    return (
      <RouterLink {...this.props} href={this.props.route} className={className}>
        { this.props.icon ? <FontIcon className="o-link__icon" value={this.props.icon} /> : null }
        { this.props.label ? <abbr>{this.props.label}</abbr> : null }
        { this.props.count && parseInt(this.props.count, 10) !== 0 ? <small>{this.props.count}</small> : null}
      </RouterLink>
    );
  }
}



