import React from 'react';
import SearchButton from './SearchButton';
import FilterButton from './FilterButton';
import SettingsButton from './SettingsButton';
import OverflowButton from './OverflowButton';
import SortButton from './SortButton';

import View from '../view';

export default class ControlsBar extends React.Component {
  static propTypes = {
    handleSearch: React.PropTypes.func,
    handleOverflow: React.PropTypes.func,
    handleSettingsSelection: React.PropTypes.func,
    handleSort: React.PropTypes.func,
    handleFilter: React.PropTypes.func,
    overflowOptions: React.PropTypes.array,
    searchable: React.PropTypes.bool,
    filterable: React.PropTypes.bool,
    configurable: React.PropTypes.bool,
    sortable: React.PropTypes.bool,
    showOverflow: React.PropTypes.bool,
    title: React.PropTypes.string
  };
  static defaultProps = {
    configurable: true,
    searchable: true,
    filterable: true,
    sortable: true,
    showOverflow: true,
    title: 'Table Title'
  };
  render() {
    return (
      <View className="c-controls-bar">
        { this.props.title ?
          <h3>{this.props.title} </h3> : null }
        { this.props.searchable ?
          <SearchButton
          onChange={this.props.handleSearch}
          expanded={false}
          /> : null }
        { this.props.sortable ? 
          <SortButton
            handleSort={this.props.handleSort}
            sortOptions={this.props.sortOptions}
            /> : null }
        { this.props.filterable ? 
          <FilterButton
            handleFilter={this.props.handleFilter}
            filterOptions={this.props.filterOptions}
           /> : null }
        { this.props.configurable ? 
          <SettingsButton
            handleSettingsSelection={this.props.handleSettingsSelection}
            settingsOptions={this.props.settingsOptions}
            /> : null }   
        { this.props.showOverflow ? 
          <OverflowButton
            handleOverflow={this.props.handleOverflow}
            overflowOptions={this.props.overflowOptions}
            /> : null }
      </View>
    );
  }
}



