import React from 'react';
export default class ListItemContent extends React.Component {
  static displayName = 'ListItemContent';
  static propTypes = {
    caption: React.PropTypes.string.isRequired,
    legend: React.PropTypes.any
  };
  render() {
    const { caption, legend } = this.props;
    return (
      <span className="o-list-content">
        <span className="o-list-content__caption">{caption}</span>
        <span className="o-list-conten__legend">{legend}</span>
      </span>
    );
  }
}



