import React from 'react';

export default class Text extends React.Component {
  static propTypes = {
    children: React.PropTypes.any,
    format: React.PropTypes.any
  };
  render() {
    const {format} = this.props;
    if (format === 'h1' || format === 'display1') {
      return (
        <h1 {...this.props}>
          {this.props.children}
        </h1>
      );
    } else if (format === 'h2' || format === 'display2') {
    return(<h2 {...this.props}>
        {this.props.children}
      </h2>)
    } else if (format === 'h3' || format === 'display3') {
    return (
      <h3 {...this.props}>
        {this.props.children}
      </h3>
    );
  } else if (format === 'h4' || format === 'display4') {
    return (
      <h4 {...this.props}>
        {this.props.children}
      </h4>
    );
  } else if (format === 'h5' || format === 'headline') {
    return (
      <h5 {...this.props}>
        {this.props.children}
      </h5>
    );
  } else if (format === 'h6' || format === 'subtitle') {
    return (
      <h6 {...this.props}>
        {this.props.children}
      </h6>
    );
  } else if (format === 'p' || format === 'body') {
    return (
      <p {...this.props}>
        {this.props.children}
      </p>
    );
  }
    return (
      <p {...this.props}>
        {this.props.children}
      </p>
    );
}
}



