import React from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin';
import mixin from 'mixin-decorator';

@mixin(PureRenderMixin)
export default class Input extends React.Component {
  static displayName = 'Input';
  static propTypes= {
    className: React.PropTypes.string,
    name: React.PropTypes.string,
    type: React.PropTypes.string,
    isRequired: React.PropTypes.bool,
    disabled: React.PropTypes.bool,
    floating: React.PropTypes.bool,
    multiline: React.PropTypes.bool,
    label: React.PropTypes.string,
    errorMsg: React.PropTypes.string,

    value: React.PropTypes.any,
    maskPattern: React.PropTypes.string,
    displayValue: React.PropTypes.any,

    onBlur: React.PropTypes.func,
    onChange: React.PropTypes.func,
    onFocus: React.PropTypes.func,
    onKeyPress: React.PropTypes.func,
    validation: React.PropTypes.func,
    children: React.PropTypes.any
  };

  static defaultProps = {
    className: '',
    disabled: false,
    floating: true,
    multiline: false,
    required: false,
    type: 'text'
  };

  renderInput = () => {
    let input;
    let className = 'o-input__body ';
    if (this.props.className) { className += this.props.className; }
    if (this.props.value) className += ' is-active';
    if (this.props.multiline) {
      input = (
        <textarea
          role="input"
          {...this.props}
          className={className}
          onChange={this.props.onChange ? this.props.onChange.bind(this, this.props.name) : null}
          />
      );
    } else {
      input = (
        <input
          role="input"
          {...this.props}
          className={className}
          onChange={this.props.onChange ? this.props.onChange.bind(this, this.props.name) : null}
          />
      );
    }
    return input;
  };
  renderError = () => {
    let errorMsg = null;
    if (this.props.errorMsg) {
      errorMsg = this.props.errorMsg;
    } else if (this.props.validation) {
      errorMsg = this.props.validation(this.props.value);
      if (!errorMsg) {
        if (this.props.isRequired && (!this.props.value || this.props.value.length === 0)) {
          errorMsg = 'This field is required.';
        }
      }
    } else if (this.props.isRequired && (!this.props.value || this.props.value.length === 0)) {
      errorMsg = 'This field is required.';
    }
    return errorMsg;
  };
  render() {
    let className = 'o-input';
    let labelClassName = 'o-input__label';
    let errorClass = 'o-input__error';
    if (this.props.value) className += ' has-content';
    if (!this.props.floating) labelClassName += `--floating`;
    if (this.props.className) className += ` ${this.props.className}`;
    if (this.props.disabled) className += ` is-disable`;
    if (this.props.type === 'hidden') className += ` is-hidden`;
    const error = this.renderError();
    if (error) {
      errorClass = 'o-input__error is-errored';
    } else {
      errorClass = 'o-input__error';
    }
    return (
      <div className={className}>
        { this.renderInput() }
        <span className="o-input__bar"></span>
        { this.props.label ? <label className={labelClassName}>{this.props.label}</label> : null }
        { error & this.props.value > 0 ? <span className={errorClass}>{error}</span> : null }
      </div>
    );
  }
}



