import React from 'react';
import { View } from 'core-ui';
export default class Container extends React.Component {
  static propTypes = {
    children: React.PropTypes.any,
    direction: React.PropTypes.string,
    noWrap: React.PropTypes.bool,
    fullscreen: React.PropTypes.bool,
    className: React.PropTypes.string
  };
  render() {
    let className = this.props.className ? `${this.props.className} o-container` : 'o-container';
    if (this.props.direction) { className += `--${this.props.direction}`; }
    if (this.props.noWrap) { className += ' noWrap'; }
    if (this.props.fullscreen) { className += ' isFullScreen'; }
    return (
      <View {...this.props} className = {className} >
        {this.props.children}
      </View>
    );
  }
}



