import React from 'react';
import {
  Drawer,
  View,
  List,
  ListSubHeader,
  ListItem
} from 'core-ui';

export default class NavMenu extends React.Component {
  static propTypes = {
    links: React.PropTypes.array,
    isDrawerActive: React.PropTypes.bool,
    onOverlayClick: React.PropTypes.func
  };
  static defaultProps = {
    isDrawerActive: false
  };
  render() {
    return (
      <Drawer active={this.props.isDrawerActive} onOverlayClick={this.props.onOverlayClick}>
        <View className="c-nav-menu">
          <List selectable ripple>
            <ListSubHeader caption="Main Site" />
            <ListItem
              caption="Home"
              onClick={()=> this.transitionTo('/') }
              />
          </List>
        </View>
      </Drawer>
    );
  }
}



