import React, {PropTypes} from 'react';
import FontIcon from '../font_icon';

export default class Avatar extends React.Component {
  static propTypes = {
    children: PropTypes.node,
    className: PropTypes.string,
    icon: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    image: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    title: PropTypes.string
  };
  render() {
    const {children, className, icon, image, title} = this.props;
    return (
      <div className={`o-avatar ${className}`} {...this.props}>
        {children}
        {typeof image === 'string' ? <img className="o-avatar__image" src={image} title={title} /> : image}
        {typeof icon === 'string' ? <FontIcon className="o-avatar__letter" value={icon} /> : icon}
        {title ? <span className="o-avatar__letter" >{title[0]}</span> : null}
      </div>
    );
  }
}



