import React from 'react';

export default class ListDivider extends React.Component {
  static displayName = 'ListDivider';
  static propTypes = {
    inset: React.PropTypes.bool
  };
  static defaultProps = {
    inset: false
  };
  render() {
    const className = this.props.inset ? 'o-divider--insert' : 'o-divider';
    return (<hr className={className} />);
  }
}



