import React from 'react';
import marked from 'marked';
import Input from '../input';
import Container from '../container';

export default class Markdown extends React.Component {
  static propTypes = {
    onChange: React.PropTypes.func,
    value: React.PropTypes.any.isRequired,
  };
  updateOutput = (...args) => {
    if (this.props.onChange) this.props.onChange(...args);
  };
  rawOutput = () => {
    return { __html: marked(this.props.value, {sanitize: true}) };
  };
  render() {
    return (
      <Container className="o-markdown">
       <div className="o-markdown__output" dangerouslySetInnerHTML={this.props.value ? this.rawOutput() : null}></div>
         {!this.props.compile ? <Container className="o-markdown__editor">
          <Input name="value" multiline {...this.props} onChange={this.updateOutput} value={this.props.value} />
        </Container> : null }
      </Container>
    );
  }
}



