import React from 'react';
import View from '../view';
import TableCell from './TableCell';
import Dragable from './DragableRow';
      
export default class TableRows extends React.Component {
  static propTypes = {
    dataSource: React.PropTypes.array,
    cellComponent: React.PropTypes.any,
    handleRowClick: React.PropTypes.func,
    isSearching: React.PropTypes.bool,
  };
  
  renderRows = () => {

    let data = this.props.dataSource;

    const CustomCell = this.props.cellComponent;
    const rows = data.map((row, index) => {
      const cells = Object.keys(row).map((cell, cellIndex) => {
       if (this.props.hiddenColumns.includes(cellIndex) ) {
        return null;
       }
        // if (CustomCell) {
        //   return (
        //     <CustomCell key={cellIndex}
        //       data={row[cell]}
        //       index={index}
        //       cellIndex={cellIndex} />
        //   );
        // }
        return (
          <TableCell key={cellIndex}
            data={row[cell]}
            column={cell}
            index={index}
            cellIndex={cellIndex} />
        );
      }).filter((item) => { return item;});
      return (
        <View key={index} className="o-table-rows__row" onClick={this.props.handleRowClick ? this.props.handleRowClick.bind(this, index) : null}>
          {cells}
        </View>
      );
    });
    return rows;
  };
  render() {
    console.log(this.props);
    let className = 'o-table-rows';
    if (this.props.fixedHeader) { className += ' isFixed'; }
    return (
  

            <View className={className}>
        { this.renderRows() }
             </View>
     
 
    );
  }
}



