import React from 'react';

export default class View extends React.Component {
  static propTypes = {
    children: React.PropTypes.any,
    imageUrl: React.PropTypes.string,
    className: React.PropTypes.string
  };
  render() {
    let style = null;
    if (this.props.imageUrl) {
      style = {'background': `url(${this.props.imageUrl}) no-repeat center center /cover`};
    }
    return (
      <div {...this.props} className={this.props.className} style={style}>
        {this.props.children}
      </div>
    );
  }
}



