import React from 'react';
import ReactDOM from 'react-dom';
import View from '../view';
import ItemTypes from './ItemTypes';
import { DragSource } from 'react-dnd';
import UpArrow from 'react-material-icons/icons/navigation/arrow-drop-up';
import DownArrow from 'react-material-icons/icons/navigation/arrow-drop-down';


const boxSource = {
  beginDrag(props) {
    return {
      name: props.name
    };
  },

  endDrag(props, monitor) {
    const item = monitor.getItem();
    const dropResult = monitor.getDropResult();

    if (dropResult) {
      props.handleDrag(item.name, dropResult.name);
      }
  }
}

@DragSource(ItemTypes.BOX, boxSource, (connect, monitor) => ({
  connectDragSource: connect.dragSource(),
  isDragging: monitor.isDragging()
}))
export default class TableColumn extends React.Component {
  
  static propTypes = {
    displayName: React.PropTypes.string.isRequired,
    handleColumnClick: React.PropTypes.func,
    handleDrag: React.PropTypes.func,
    index: React.PropTypes.number,
    className: React.PropTypes.string,
    data: React.PropTypes.any,
    connectDragSource: React.PropTypes.func.isRequired,
    isDragging: React.PropTypes.bool.isRequired,
    name: React.PropTypes.string.isRequired
  };
  constructor(...args) {
    super(...args);
    this.state = {
      asc: true,
      isSorted: false,
      connectDragSource: React.PropTypes.func.isRequired,
      isDragging: React.PropTypes.bool.isRequired,
      name: React.PropTypes.string.isRequired
    };
  }

    componentDidMount() {
      document.addEventListener('click', this.handleClickOutside, true);
    }

    componentWillUnmount() {
      document.removeEventListener('click', this.handleClickOutside, true);
    }

    handleClickOutside = (e) => {
      const domNode = ReactDOM.findDOMNode(this.refs.column);
      if (!domNode || !domNode.contains(e.target) ) {
        this.setState({isSorted: false});
      }
    };

  handleColumnClick = ({index, data}, event) => {
    const state = {asc: !this.state.asc, isSorted: true};
    if (this.props.handleColumnClick) { this.props.handleColumnClick({index, data, event, state}); }
    this.setState(state);
  };

  render() {
    const { isDragging, connectDragSource } = this.props;
    const { name } = this.props;
    let className = this.props.className ? this.props.className : 'o-table-header__column';
    className += ` column-${name.replace(/ /g, '-').toLowerCase()}`;
    if (this.state.isSorted && this.state.asc) { className += ' is-asc'; }
    if (this.state.isSorted && !this.state.asc) { className += ' is-desc'; }
    const handleClick = this.handleColumnClick.bind(this, {index: this.props.index, data: this.props.data});
    let icon = null;
    if (this.state.isSorted && this.state.asc === true) {
      icon = (<UpArrow/>);
    }
    if (this.state.isSorted && this.state.asc === false) {
      icon = (<DownArrow/>);
    }
    return (
      connectDragSource(
        <span className={className}
          onClick={handleClick} >
          <span ref="column" >
          {this.props.displayName}</span>
          {icon}
        </span>
      )
    );
  }
}



