import React from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin';
import mixin from 'mixin-decorator';

@mixin(PureRenderMixin)
export default class Form extends React.Component {
  static displayName = 'Form';
  static propTypes = {
    name: React.PropTypes.string.isRequired,
    className: React.PropTypes.string,
    onChange: React.PropTypes.func,
    onError: React.PropTypes.func,
    onSubmit: React.PropTypes.func,
    onIsValid: React.PropTypes.func,
    validate: React.PropTypes.bool,
    children: React.PropTypes.any
  };
  static defaultProps = {
    name: 'form',
    validate: true
  };
  handleSubmit = (event) => {
    event.preventDefault();
    if (this.props.onSubmit) { this.props.onSubmit(this.handleValidation); }
  };
  handleChange = (event) => {
    event.preventDefault();
    const name = event.target.name;
    const value = event.target.value;
    const form = this;
    this.props.onChange({name, value, form, event});
  };
  handleValidation = (data) => {
    console.log(data);
    if (this.props.validate) {
      const containsInvalidParams = React.Children.map(this.props.children, (child) => {
        console.log(child.props.name);
        const value = data[child.props.name];
        console.log(value);
        if (child.props.isRequired && (!value || value.length === 0)) {
          console.log('returning false');
          return false;
        } else if (child.props.validation) {
          const errorMsg = child.props.validation(child.props.value);
          if (errorMsg) { return false; }
        } else {
          return true;
        }
      }, this);
      console.log(containsInvalidParams);
      return !containsInvalidParams.includes(false);
    }
  };
  render() {
    let className = 'o-form';
    if (this.props.className) { className += this.props.className; }
    return (
      <form className={className} onSubmit={this.handleSubmit} onChange={this.handleChange}>
        { this.props.children }
      </form>
    );
  }
}



