import React from 'react';
import Overlay from '../overlay';

export default class HeroImage extends React.Component {
    render() {
        let style = {background: `url(${this.props.imageUrl}) no-repeat center center / cover`}
        return (
            <div className='c-hero-image' style={style}>
                <Overlay />
                    <div className='c-hero-image__content'>
                        {this.props.children}
                    </div>
            </div>
        );
    }
}



