import React from 'react';
import View from '../view'; 
import TableEmpty from './TableEmpty';
import TableColumns from './TableColumns';
import ControlsBar from './ControlsBar';
import TableRows from './TableRows';
//import {Table, Col, Cell, ColumnGroup } from 'fixed-data-table';

export default class Table extends React.Component {
  static propTypes = {
    dataSource: React.PropTypes.array.isRequired,
    columns: React.PropTypes.array.isRequired,
    hiddenColumns: React.PropTypes.array,

    handleSearch: React.PropTypes.func,
    handleFilter: React.PropTypes.func,
    handleGroup: React.PropTypes.func,
    handleSort: React.PropTypes.func,
    handleColumnClick: React.PropTypes.func,
    handleSettingsSelection: React.PropTypes.func,
    handlePaginate: React.PropTypes.func,
    handleRowClick: React.PropTypes.func,

    searchable: React.PropTypes.bool,
    filterable: React.PropTypes.bool,
    sortable: React.PropTypes.bool,
    groupable: React.PropTypes.bool,
    dropable: React.PropTypes.bool,
    scrollable: React.PropTypes.bool,
    resizable: React.PropTypes.bool,
    configurable: React.PropTypes.bool,
    exportable: React.PropTypes.bool,
    infinite: React.PropTypes.bool,
    paginate: React.PropTypes.bool,
    dragableRows: React.PropTypes.bool,
    showRowCount: React.PropTypes.bool,

    highlightInline: React.PropTypes.bool,
    fixedHeader: React.PropTypes.bool,
    bodyHeight: React.PropTypes.number,
    resultsPerPage: React.PropTypes.number,
    endScrollTimeout: React.PropTypes.number,

    columnComponent: React.PropTypes.any,
    loadingComponent: React.PropTypes.any,
    emptyComponent: React.PropTypes.any,
    dataComponent: React.PropTypes.any,
    controlsComponent: React.PropTypes.any,
    settingsComponent: React.PropTypes.any,
    rowComponent: React.PropTypes.any,
    paginationComponent: React.PropTypes.any,

    tableClassName: React.PropTypes.string,
    headerClassName: React.PropTypes.string,
    rowClassName: React.PropTypes.string,
    dataClassName: React.PropTypes.string,
  };
  static defaultProps = {
    searchable: true,
    filterable: true,
    sortable: true,
    groupable: true,
    dropable: true,
    resizable: true,
    configurable: true,
    exportable: true,
    infinite: true,
    paginate: false,
    dragableRows: false,

    highlightInline: false,
    fixedHeader: true,
    bodyHeight: 600,
    endScrollTimeout: 300,
    showRowCount: false
  };
  constructor(...args) {
    super(...args);
    this.state = { 
      scrollTop: 0,
      isScrolling: false,
      isSearching: false,
      currentSearchQuery: '', 
      previousSearchQuery: '',
    };
  }
  onPageScrollEnd = () => {
    const scrollTop = ViewportMetrics.currentScrollTop;
    if (scrollTop === this.state.scrollTop) {
      win.clearTimeout(this._pageScrollTimeout);
      this.setState({ isScrolling: false });
      if (typeof this.onPageScrollEnd === 'function') {
        this.onPageScrollEnd(scrollTop);
      }
    }
  };
  onPageScroll = () => {
    const scrollTop = ViewportMetrics.currentScrollTop;
    this.setState({
      scrollTop: scrollTop,
      isScrolling: true
    });

    win.clearTimeout(this.pageScrollTimeout);
    this._pageScrollTimeout = win.setTimeout(this._onPageScrollEnd, this.props.endScrollTimeout);

    if (typeof this.onPageScroll === 'function') {
      this.onPageScroll(scrollTop);
    }
  };
  handleSort = (column) => {
    console.log('running');
    this.setState({isSoriting: true, sortedColumn: column});

  };
  render() {
    return (
      <View ref="table" className="o-table">
        {/*this.props.dataSource.length === 0 ? 
         /*<TableEmpty 
            emptyComponent={this.props.emptyComponent}
            /> : null }*/}
        <ControlsBar
          title={this.props.title}
          handleSearch={this.props.handleSearch}
          handleFilter={this.props.handleFilter}
          handleGroup={this.props.handleGroup}
          handleSort ={this.props.handleSort}

          searchable={this.props.searchable}
          filterable={this.props.filterable}
          sortable={this.props.sortable}
          exportable={this.props.exportable}
          configurable={this.props.configurable}
          />
      
          <TableColumns
          handleReset ={this.props.handleReset}
          handleColumnClick={this.props.handleSort}
          columns={this.props.columns}
          handleDrag={this.props.handleDrag}
          hiddenColumns={this.props.hiddenColumns}
          fixedHeader={this.props.fixedHeader}
          resizable={this.props.resizable}
          dataSource={this.props.dataSource}
          columnComponent={this.props.columnComponent}
          /> 
    
        <TableRows
          dataSource={this.props.dataSource}
          columns={this.props.columns}
          isSearching = {this.state.isSearching}
          highlightInline={this.props.highlightInline}
          hiddenColumns={this.props.hiddenColumns}
          fixedHeader={this.props.fixedHeader}
          handleRowClick={this.props.handleRowClick}
          scrollable={this.props.scrollable}
          infinite={this.props.infinite}
          dragableRows={this.props.dragableRows}
          bodyHeight={this.props.bodyHeight}
          scrollPosition={this.state.scrollTop}
          isScrolling={this.state.isScrolling}
          rowComponent={this.props.rowComponent}
          /> 

        { /* this.props.infinite ? null : 
          <TablePagination 
            handlePaginate={this.props.handlePaginate} 
            paginate={this.props.paginate}
            resultsPerPage={this.props.resultsPerPage}
            /> */}
      </View>
    );
  }
}



